import React, { useEffect, useState } from 'react';
import axios from 'axios';
import "./Servers.css"

 
const BASE_URL = 'https://api.battlemetrics.com/servers/660901?include=player';



const RustafiedEUOdd = () => {
    const [data, setData] = useState(null);

    useEffect(() => {
        axios.get(`${BASE_URL}`)
            .then(({ data }) => setData(data))
            .catch(console.error)

    }, []);

    return (
    
    <div class="">
      {
        data ?
      <div class="server"> 
        <div className="server_name">
          <h1>{data.data.attributes.name}</h1> 
       </div> 
       <div className="players_online">
         {data.data.attributes.players}
         /{data.data.attributes.maxPlayers}
        
         ({data.data.attributes.details.rust_queued_players})
       </div> 
       <div className="wipe">
           Last wipe: {data.data.attributes.details.rust_last_wipe}
       </div>
      </div>
      : <p class="server">please wait....</p> 
      }
    
    </div>
    
    );
};

export default RustafiedEUOdd ;